import { bindings, hook } from '@vl/redata';

const bindData = bindings({
  userTransactionFilter: {
    rules: [
      [
        'data',
        {
          data: {
            '@items': hook((ctx) => {
              const statusData = [
                { label: 'Chưa xác định', value: 'unknown' },
                { label: 'Cuộc gọi nhỡ', value: 'reject' },
                { label: 'Đang xử lý', value: 'processing' },
                { label: 'Hoàn thành', value: 'completed' },
              ];
              const result = {
                statusData,
                onChangeDate: (date) => {
                  ctx.apply('@form.handleChange', 'createdAt')(date ? date.format('YYYY-MM-DD') : '');
                },
                onChangeStatus: (status) => {
                  const newStatus = status || '';
                  ctx.apply('@form.handleChange', 'status')(newStatus);
                },
              };
              return result;
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
