import React from 'react';

import DIV from '@vl/redata/DIV.macro';
import { UnAuthRedirect } from '@uz/unitz-components-web/AppRedirect';
import { ctx } from '@vl/redata';
import { resable } from '@vl/redata/RESABLE';
import RESABLE from '@vl/redata/RESABLE.macro';
import UserProfile from '@uz/unitz-pages/UserProfile';
import UserTransactionHistory from '@uz/unitz-pages/UserTransactionHistory';
import UserCallHistory from '@uz/unitz-pages/UserCallHistory';
import UserNotification from '@uz/unitz-pages/UserNotification';
import UserSumary from '@uz/unitz-components-web/UserSumary';
import UserMenus from '@uz/unitz-components-web/UserMenus';
import { Layout } from 'antd';
import _ from 'lodash';

const { Sider, Content } = Layout;

const Index = () => {
  return (
    <DIV className="profileMenuModel">
      <UnAuthRedirect to={ctx.apply('authModel.generateLoginRedirectUrl')}>
        <Layout className="py-0 bg-transparent">
          <Sider
            style={{ backgroundColor: 'transparent' }}
            className="bg-transparent max-lg:hidden"
            theme="light"
            width={264}
          >
            <RESABLE>
              <DIV>
                {ctx.debug(() => {
                  const wallets = resable.for('wallets')(ctx.get('wallets'), []);
                  ctx.set('wallets', _.get(wallets, '0.wallets', []));
                })}
                <UserSumary />
              </DIV>
            </RESABLE>
            <UserMenus />
          </Sider>
          <Content>
            <DIV>
              <DIV className="infoStep">
                <UserProfile />
              </DIV>
              <DIV className="notificationStep">
                <UserNotification />
              </DIV>
              <DIV className="transactionStep">
                <UserTransactionHistory />
              </DIV>
              <DIV className="historyStep">
                <UserCallHistory />
              </DIV>
            </DIV>
          </Content>
        </Layout>
      </UnAuthRedirect>
    </DIV>
  );
};

export default Index;
